import React from 'react'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import {
  SectionWrapper,
  ContentWrapper,
  ImageWrapper,
  Text,
} from './styles'
import {
  SectionsTitle,
  SectionsSubtitle,
  Container,
} from '../../styles'

export const AboutUs = () => {

  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "about-us.png" }) {
        childImageSharp {
          fluid(maxWidth: 640) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Container>
      <SectionWrapper id="aboutUs">
        <ContentWrapper>
          <SectionsTitle>
            Few words about us
          </SectionsTitle>
          <SectionsSubtitle>
            Oorganized is a free property management platform for the global market.
          </SectionsSubtitle>
          <Text>
            This software ismade with love for landlords, tenants and property managers.
            Weoffer you aunique solution with all needed functionalities tomanage your properties
            just for free. Wespent 12 years ourselves suffering from chaos inour excel files recording
            tenants information, monthly payments, deposits, viewings, etc. Finally, weunited everything
            inone place and enjoy it. Join our happy team and use afree property management solution forever.
          </Text>
        </ContentWrapper>
        <ImageWrapper>
          <Img fluid={data.placeholderImage.childImageSharp.fluid} />
        </ImageWrapper>
      </SectionWrapper>
    </Container>
  )
}